 .body7 {
           font-family: Arial, sans-serif;
           margin: 0;
           padding: 0;
           display: flex;
           justify-content: center;
           align-items: center;
           height: 100vh;
           background-color: #f4f4f4;
         }

         .booking-container {
           text-align: center;
           padding: 20px;
           border: 1px solid #ccc;
           border-radius: 5px;
           background-color: #fff;
         }

         .label {
           display: block;
           margin-bottom: 10px;
         }

         .input, .select {
           width: 100%;
           padding: 10px;
           margin-bottom: 15px;
           box-sizing: border-box;
         }

         .button {
           padding: 10px;
           background-color: #4caf50;
           color: #fff;
           border: none;
           border-radius: 5px;
           cursor: pointer;
         }